
.App {
  text-align: center;
  min-height: 100vh;
  max-width: 100%;
  overflow: hidden;
  font-family: MyFont !important; 

}
.textoEquipo{
  font-size: 1rem;
  padding: 50px;
  text-align: justify;
}
@media screen and (max-width: 900px) {
  #MetoBoxContainer {
    flex-direction: column !important;
  }
  .avatar{
    flex-direction: column !important;
  }
}
#MetoBoxContainer {
  padding-right: 60px;
  padding-left: 60px;
}
#Abrev{
  font-size: 3rem;
  color: white;
  font-weight: bold;

}
#Valor{
  font-size:  3.5rem;
  font-weight: bold;
  font-style: italic;
  color: rgb(153,153,153);
  margin: 40px;
}
#DescripcionValor{
  color: rgb(153,153,153);
  font-size: 2rem;
}
.progreso{
  height: 150px;
  width: calc(100vw - 80px);
  background: #e73944
}
#avance{
  height: 100%;
  width: 33.33333%;
  background: #f9b427
}
.third{
  width: 33.33333% !important;
}
.sixty{
  width: 66.66666% !important;
}
.full{
  width: 100% !important;
}
.ValoresContainer{
  background-color: white;
    height: 100vh;
    display: flex;
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    display: flex;
    align-items: center;
    align-items: center;
}
.boxy:hover{
  background-color: #e73944 !important;
}
.boxy {
  background-color: #22b5c0;
  height: 100% !important;
  width: 100% !important;
}
.Manifesto{
  font-size: 1.5em;
  padding-right: 10vw;
  position: relative;
  padding-left: 10vw;
  padding-top: 100px;
  padding-bottom:100px;
  transform: translateY(80vh);
  background: #008590;
  color:white;
  max-width:800px;
  display: block;
  z-index: 2;
  margin: auto;
  transition: opacity 1s;
}
.box{
  padding: 30px;
  margin: 20px;
}
#Busca{
  background-color: #f9b427;
    color: white;
    font-size: 1.2em;
    margin-left: -20px;
    width: 85vw;
    max-width: 600px;
    padding-left: 15vw;
    transform: translateX(-100vw);
}
#Hacemos{
  background-color: #ee781f;
    color: white;
    font-size: 1.2em;
    transform: translateX(100vw);
    margin-right: -20px;
    float: right;
    width: 85vw;
    max-width: 600px;
    padding-right: 15vw;
    
}
.Visible{
  opacity: 1 !important;
}
.TextRight{
  text-align: right;
}
.TextLeft{
  text-align: left;
}
#ManifestoText{
  opacity:0;
  transition: opacity 1s;
}
.ManifestoVisible{
  opacity: 1 !important;
}
.Bienvenidos{
  font-size: 2.5em;
  position: absolute;
  width: 100vw;
  z-index: 1;
  text-align: center;
  top:calc(150vh - 1.25em);
}
@font-face {
  font-family: MyFont;
  src: url('./font/Champagne\ &\ Limousines.ttf');
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.AppHeader{
  background-color: black;
  height: 300px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.circle{
  height: 1.25em;
    width: 1.25em;
    border-width: 0.2em;
    margin: 0.5em;
    border-style: solid;
    border-color: rgb(153,153,153);
    border-radius: 0.625em;
  transition: all 0.5s;

}
.circleActive{
  background-color: rgb(153,153,153) !important;
}
.circleWrapper{
  position:fixed;
  top: calc(50% - 7em);
  transition: opacity 0.5s;
  opacity: 0;
  right: 15px;
}
.circleVisible{
  opacity: 1 !important;
  z-index: 99999999999999999999;
}
.paperUP{
  animation-name: App-logo-spin;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  transition: all 1s;
}
.paperDOWN{
  animation-name: App-logo-spin2;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  transition: all 1s;
}
.parallax_group{
  position: relative;
  height: 100vh;
  transition: transform 0.5s;
  transform-style: preserve-3d;
}
#Hlayer2 {
  transform: translateZ(0);
}
#Hlayer1 {
  transform: translateZ(-1px) scale(2);
}
.App-link {
  color: #61dafb;
}
